import React,{useState} from 'react'

import './book.css'


 function Book() {
     const[book,setBook]=useState()
     const [booklist,setbooklist]=useState([])
     
     const HandleBook=(e)=>{
         setBook(e.target.value)
     }
     const HandleSubmit=(e)=>{
         e.preventDefault()
         if(book!==""){
             const booklists={
                 id:Math.floor(Math.random()*1000),
                 value:book,
                 
                 
             }
             setbooklist([...booklist,booklists])

         }
         
     }
     const Deletetask=(e,id)=>{
         setbooklist(booklist.filter((t)=>t.id!==id))
     }
     
    return (<div>
       
        <div><input type="text" name="book" value={book} onChange={HandleBook}/>
        <button onClick={HandleSubmit}>Add Book</button>
        </div>
        <div>
        {booklist!==[]?
           <ul className="book">{booklist.map(books=><li key={books.id}>{books.value}
           <button onClick={e=>Deletetask(e,books.id)}>Delete Book</button>
                     
           </li>)}
           </ul>  
           
           :null}      
        
        </div>

        
        </div>
        
    )
}
export default Book
